//      off    /  
%ir {
  display: inline-block;
  overflow: hidden;
  text-indent: -9999em;
  .no-img & {
    overflow: auto;
    text-indent: 0;
  }
//  position     
}
%ir-txt {
  display: block;
  overflow: hidden;
  position: relative;
  z-index: -1;
  width: 100%;
  height: 100%;
}
//        / , 
%ir-img {
  overflow: hidden;
  position: absolute;
  left: -999px;
  width: 0;
  height: 1px;
  margin: 0;
  padding: 0;
}
