// ========================================================================
// [sequence] | 3-3---MVP
// ========================================================================
@keyframes motion-img{
  0%{
    background-position: 0 0;
  }
  100%{
    background-position: 0 -410px;
  }
}
//  15 /    14
@keyframes motion-img{
  $total: 14;
  @for $i from 0 through $total {
    #{$i*7.1}% {
      background-position: 0 (-332px * $i);
    }
  }
  100% {
    background-position: 0 (-332px * $total);
  }
}
